var util = require('./util');

var STEPS = [
  [31536e6, 5],  // 1-year
  [7776e6, 4],   // 3-month
  [2592e6, 4],   // 1-month
  [12096e5, 3],  // 2-week
  [6048e5, 3],   // 1-week
  [1728e5, 3],   // 2-day
  [864e5, 3],    // 1-day
  [432e5, 2],    // 12-hour
  [216e5, 2],    // 6-hour
  [108e5, 2],    // 3-hour
  [36e5, 2],     // 1-hour
  [18e5, 1],     // 30-minute
  [9e5, 1],      // 15-minute
  [3e5, 1],      // 5-minute
  [6e4, 1],      // 1-minute
  [3e4, 0],      // 30-second
  [15e3, 0],     // 15-second
  [5e3, 0],      // 5-second
  [1e3, 0]       // 1-second
];

var entries = [
  {
    type: "second",
    minstep: 1,
    format: "%Y %b %-d %H:%M:%S.%L",
    date: function(d) {
      return new Date(d * 1e3);
    },
    unit: function(d) {
      return (+d / 1e3);
    }
  },
  {
    type: "minute",
    minstep: 1,
    format: "%Y %b %-d %H:%M",
    date: function(d) {
      return new Date(d * 6e4);
    },
    unit: function(d) {
      return ~~(+d / 6e4);
    }
  },
  {
    type: "hour",
    minstep: 1,
    format: "%Y %b %-d %H:00",
    date: function(d) {
      return new Date(d * 36e5);
    },
    unit: function(d) {
      return ~~(+d / 36e5);
    }
  },
  {
    type: "day",
    minstep: 1,
    step: [1, 7],
    format: "%Y %b %-d",
    date: function(d) {
      return new Date(d * 864e5);
    },
    unit: function(d) {
      return ~~(+d / 864e5);
    }
  },
  {
    type: "month",
    minstep: 1,
    step: [1, 3, 6],
    format: "%b %Y",
    date: function(d) {
      return new Date(Date.UTC(~~(d / 12), d % 12, 1));
    },
    unit: function(d) {
      if (util.isNumber(d)) d = new Date(d);
      return 12 * d.getUTCFullYear() + d.getUTCMonth();
    }
  },
  {
    type: "year",
    minstep: 1,
    format: "%Y",
    date: function(d) {
      return new Date(Date.UTC(d, 0, 1));
    },
    unit: function(d) {
      return (util.isNumber(d) ? new Date(d) : d).getUTCFullYear();
    }
  }
];

var minuteOfHour = {
  type: "minuteOfHour",
  min: 0,
  max: 59,
  minstep: 1,
  format: "%M",
  date: function(d) {
    return new Date(Date.UTC(1970, 0, 1, 0, d));
  },
  unit: function(d) {
    return (util.isNumber(d) ? new Date(d) : d).getUTCMinutes();
  }
};

var hourOfDay = {
  type: "hourOfDay",
  min: 0,
  max: 23,
  minstep: 1,
  format: "%H",
  date: function(d) {
    return new Date(Date.UTC(1970, 0, 1, d));
  },
  unit: function(d) {
    return (util.isNumber(d) ? new Date(d) : d).getUTCHours();
  }
};

var dayOfWeek = {
  type: "dayOfWeek",
  min: 0,
  max: 6,
  step: [1],
  format: "%a",
  date: function(d) {
    return new Date(Date.UTC(1970, 0, 4 + d));
  },
  unit: function(d) {
    return (util.isNumber(d) ? new Date(d) : d).getUTCDay();
  }
};

var dayOfMonth = {
  type: "dayOfMonth",
  min: 1,
  max: 31,
  step: [1],
  format: "%-d",
  date: function(d) {
    return new Date(Date.UTC(1970, 0, d));
  },
  unit: function(d) {
    return (util.isNumber(d) ? new Date(d) : d).getUTCDate();
  }
};

var monthOfYear = {
  type: "monthOfYear",
  min: 0,
  max: 11,
  step: [1],
  format: "%b",
  date: function(d) {
    return new Date(Date.UTC(1970, d % 12, 1));
  },
  unit: function(d) {
    return (util.isNumber(d) ? new Date(d) : d).getUTCMonth();
  }
};

var units = {
  "second":       entries[0],
  "minute":       entries[1],
  "hour":         entries[2],
  "day":          entries[3],
  "month":        entries[4],
  "year":         entries[5],
  "minuteOfHour": minuteOfHour,
  "hourOfDay":    hourOfDay,
  "dayOfWeek":    dayOfWeek,
  "dayOfMonth":   dayOfMonth,
  "monthOfYear":  monthOfYear,
  "timesteps":    entries
};

units.find = function(span, minb, maxb) {
  var i, len, bins, step = STEPS[0];

  for (i = 1, len = STEPS.length; i < len; ++i) {
    step = STEPS[i];
    if (span > step[0]) {
      bins = span / step[0];
      if (bins > maxb) {
        return entries[STEPS[i - 1][1]];
      }
      if (bins >= minb) {
        return entries[step[1]];
      }
    }
  }
  return entries[STEPS[STEPS.length - 1][1]];
};

module.exports = units;
